.header-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient( rgba(0, 0, 0, 0.0), rgba(0, 0, 0, 0.25) ), url("../../../assets/york-background-trimmed-2.jpg");
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 800px;
}

.welcome-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* TODO: decide whether to use background blur here to improve legibility */
    /* width: 100%;
    backdrop-filter: blur(1px); */
}

.welcome-card-text {
    font-size: 1em;
    color: white;
    text-align: center;
    max-width: 40%;
}

.welcome-card-text.title {
    text-transform: uppercase;
    text-decoration: underline;
    text-underline-offset: 3px;
    margin-bottom: 5px;
    font-size: 2.4em;
    letter-spacing: 1px;
}

.welcome-card-support-button {
    background-color: #297045;
    color: white;
    margin: 10px;
    padding: 17px;
    transition: outline 400ms;
    outline: solid 2px white;
    font-weight: 900;
    cursor: pointer;
    text-decoration: none;
}

.welcome-card-support-button:hover {
    transition: outline 400ms;
    outline: solid 2px #ECBA82;
}

.who-are-we-container {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 70px;
    padding-top: 30px;
}

.who-are-we-image {
    height: auto;
    max-width: 35%;
    max-height: 600px;
}

.who-are-we-text-container {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 23%;
}

.who-are-we-text {
    text-align: left;
}

.who-are-we-text.body {
    font-size: 1em;
    padding: 10px 0px;
}

.who-are-we-text.title {
    font-size: 3em;
    letter-spacing: 1px;
    color: rgb(36, 36, 36);
    text-transform: uppercase;
    text-decoration: underline solid black 3px;
    text-underline-offset: 3px;
}

.who-are-we-gallery-image {
    max-height: 300px;
    float: left;
}

.who-are-we-button {
    background-color: #297045;
    color: white;
    padding: 17px;
    transition: outline 400ms;
    outline: solid 2px white;
    text-transform: uppercase;
    text-align: center;
    font-weight: 900;
    cursor: pointer;
    text-decoration: none;
}

.who-are-we-button:hover {
    transition: outline 400ms;
    outline: solid 2px #ECBA82;
}

.gallery-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #297045;
    padding: 20px;
}

.gallery-image {
    height: 500px;
    object-fit: none;
    width: 100%;
}

.gallery-text.title {
    font-size: 3em;
    letter-spacing: 1px;
    color: rgb(233, 233, 233);
    text-transform: uppercase;
    text-decoration: underline solid rgb(233, 233, 233) 3px;
    text-underline-offset: 3px;
}

.gallery-carousel {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 10px;
}

.outer-news-container{ 
    display: flex;
    justify-content: center;
    margin: 50px 0px;
}

.news-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    max-width: 70%;
}

@media screen and (max-width: 970px) {

    .welcome-card-text {
        max-width: 80%;
        margin-left: 0;
    }


    .news-container {
        max-width: 100%;
    }

    
}

@media screen and (max-width: 1200px) {
    .who-are-we-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        column-gap: 70px;
        padding-top: 30px;
    }

    .who-are-we-text-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 90%;
    }

    .who-are-we-text {
        text-align: center;
    }

    .who-are-we-text.body {
        max-width: 70%;
    }

    .who-are-we-image {
        max-width: 90%;
        padding-bottom: 20px;
    }

    .who-are-we-button {
        margin: 20px;
    }
}