.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    width: 100%;
    background-color: #297045;
    margin-bottom: 1px;
}

.navbar-items-list {
    display: flex;
    list-style: none;
    padding-left: 20px;
    gap: 50px;
}

.navbar-item-link {
    display: inline-block;
    color: white;
    text-decoration: none;
    font-size: 1.1em;
    text-decoration: underline rgba(100%, 100%, 100%, 0) 2px;
    text-underline-offset: 4px;
    transition: text-decoration 200ms;
}

.navbar-item-link:hover {
    text-decoration: underline rgba(100%, 100%, 100%, 1) 2px;
    text-underline-offset: 4px;
    transition: text-decoration 200ms;
}

.dropdown-menu-icon {
    display: none;
}

.navbar-logo {
    padding: 0px 20px;
}

.donate-button {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #204e4a;
    padding-left: 2%;
    padding-right: 2%;
    margin-left: auto;
    height: 100%;
}

/* styling for phones */
@media screen and (max-width: 970px) {
    
    .dropdown-menu-icon {
        display: block;
        position: absolute;
        color: white;
        top: 0;
        right: 0;
        transform: translate(-100%, 55%);
        font-size: 1.5em;
        cursor: pointer;
    }

    .fa-times {
        font-size: 1.3em;
    }

    .navbar-container {
        flex-direction: column;
    }

    .navbar-items-list {
        flex-direction: column;
        width: 100%;
        position: absolute;
        padding-bottom: 15px;
        top: 70px;
        opacity: 0;
        left: -100%;
    }

    /* make this also fade out */
    .navbar-items-list.active {
        background: #297045;
        left: 0;
        transition: left 0.5s ease, opacity 0.5s ease;
        opacity: 1;
    }

    .navbar-item {
        text-align: center;
        padding: 1rem;
        width: 1;
        display: table;
    }

    .donate-button {
        display: none;
    }
}
