.card-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.card-container:hover > .card-title{
  text-decoration: underline;
}

.card-title {
  font-size: 1.3rem;
}

.card-image {
  height: 200px;
  object-fit: none;
  width: 100%;
}

.card-preview {
  overflow: hidden;
  text-overflow: ellipsis;
}
